import React from "react";
import "./DrumMachine.scss";
import Icon from "./Icon.js";
import DrumPad from "./DrumPad.js";
import Interface from "./Interface.js";
import { heaterKit, pianoKit } from "./audioSrc.js";
class DrumMachine extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			power: true,
			mode: "heaterKit",
			volume: 0.5,
			display: "",
		};
		this.handleClick = this.handleClick.bind(this);
		this.handleChange = this.handleChange.bind(this);
		this.handleSwitch = this.handleSwitch.bind(this);
	}
	componentDidMount() {
		document.addEventListener("keydown", (event) => {
			if (
				/[qweasdzxc]/.test(event.key)
			)
				this.handleClick(event.key);
		});
	}
	handleClick(event) {
		let id;
		let target;
		if (event.target === undefined) {
			id = event;
			target = document.querySelector("#" + event);
		} else {
			id = event.target.id;
			target = event.target;
		}
		this.setState((prevState) => ({
			display:
				prevState.mode === "heaterKit"
					? heaterKit[id].display
					: pianoKit[id].display,
		}));
		target.style.boxShadow = "none";
		setTimeout(() => {
			target.style = {};
		}, 100);
		if (this.state.power) {
			target.firstChild.volume = this.state.volume;
			target.style.backgroundColor = "orange";
			target.firstChild.currentTime = 0;
			target.firstChild.play();
		}
	}
	handleSwitch(event) {
		let state = event.target;
		if (state.id === "switch_2") {
			this.setState({
				mode: state.style.float === "right" ? "heaterKit" : "pianoKit",
				display:
					state.style.float === "right"
						? "Heater Kit"
						: "Smooth Piano Kit",
			});
		} else {
			this.setState({
				power: state.style.float === "right" ? true : false,
			});
		}
		state.style.float = state.style.float === "left" ? "right" : "left";
	}
	handleChange(event) {
		this.setState({
			volume: parseFloat(event.target.value),
			display: `Volume: ${(this.state.volume * 100).toFixed(0)}`,
		});
	}
	render() {
		return (
			<div id="drum-machine">
				<Icon />
				<div id="main">
					<DrumPad
						power={this.state.power}
						mode={this.state.mode}
						handleClick={this.handleClick}
					/>
					<Interface
						config={this.state}
						handleSwitch={this.handleSwitch}
						handleChange={this.handleChange}
					/>
				</div>
			</div>
		);
	}
}
export default DrumMachine;
