import React from "react";
import "./DrumPad.scss";
import { heaterKit, pianoKit } from "./audioSrc.js";
function DrumPad(props) {
  let keys = ["q", "w", "e", "a", "s", "d", "z", "x", "c"];
  let drumPads = keys.map((key) => {
    return (
      <button
        id={key}
        key={key}
        className="drum-pad"
        onClick={props.handleClick}
      >
        <audio
          src={
            props.mode === "heaterKit" ? heaterKit[key].src : pianoKit[key].src
          }
          id={key.toUpperCase()}
          className="clip"
        ></audio>
        {key.toUpperCase()}
      </button>
    );
  });
  return <div id="drum-pad-container">{drumPads}</div>;
}

export default DrumPad;
