import React from "react";
import "./Interface.scss";
function Interface(props) {
	return (
		<div id="interface">
			<div id="power">
				<p>Power</p>
				<div id="power-switch">
					<div
						id="switch_1"
						style={{ float: "left" }}
						onClick={props.handleSwitch}
					></div>
				</div>
			</div>
			<p id="display">{props.config.power ? props.config.display : ""}</p>
			<div id="volume-container">
				<input
					type="range"
					min="0"
					max="1"
					step="0.01"
					id="volume"
					value={props.config.volume}
					onChange={props.handleChange}
				></input>
			</div>
			<div id="bank">
				<p>Bank</p>
				<div id="bank-switch">
					<div
						id="switch_2"
						style={{ float: "left" }}
						onClick={props.handleSwitch}
					></div>
				</div>
			</div>
		</div>
	);
}

export default Interface;
